.error {
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 96px;
    font-weight: 700;
    margin: 75px 0;
  }

  h2 {
    font-size: 18px;
    text-align: center;
    margin-bottom: 100px;
    color: #FF6060;
  }

  a {
    text-decoration: underline;
    margin-bottom: 100px;
  }
}

@media (min-width: 1200px) {
  .error {
    h1 {
      font-size: 230px;
      margin: 150px 0;
    }

    h2 {
      font-size: 36px;
    }
  }
}