.about-collapse {
  display: flex;
  flex-direction: column;
  margin: 15px auto;
  max-width: 1025px;

  li {
    width: 100%;

    button {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      cursor: pointer;
      text-align: left;
      border-radius: 5px;
      border: none;
      background-color: #FF6060;
      padding: 8px 0 8px 10px;
      margin: 10px 0;
      font-size: 15px;
      font-family: 'montserrat', sans-serif;
      font-weight: 700;
      color: #FFF;

      img {
        rotate: 90deg;
        width: 10px;
        position: absolute;
        right: 15px;
      }
    }
  }
}

@media (min-width: 762px) {
  .about-collapse button {
    height: 40px;
  }
}