.header {
  padding: 30px 20px;
  display: flex;
  justify-content: space-between; 
  align-items: center;

  ul {
    display: flex;
  }

  li a {
    color: #FF6060;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 14px;
  }

  img {
    width: 145px;
  }

  .aboutlink {
    margin-left: 25px;
  }

  .active {
    text-decoration: underline;
  }
}

@media screen and (min-width: 768px) {
  .header {
    max-width: 1200px;
    font-size: 20px;
    margin: auto;

    li a {
      text-transform: capitalize;
      font-size: 24px;
    }

    img {
      width: 210px;
    }

    .aboutlink {
      margin-left: 65px;
    }

    nav a {
      text-transform: none;
    }
  }
}
