.slideshow {
  position: relative;
  margin: auto;

  .slideshow-image {
    width: 100%;
    object-fit: cover;
    height: 255px;
    border-radius: 15px;
  }

  .arrow-left {
    width: 20px;
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    cursor: pointer;
  }
  
  .arrow-right {
    width: 20px;
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    cursor: pointer;
  }  
}

.count {
  display: none;
}

@media (min-width: 768px) {
  .slideshow {
    .slideshow-image {
      height: 410px;
    }
  }
}

@media (min-width: 1200px) {
  .count {
    display: flex;
    position: absolute;
    top: 333px;
    right: calc((97%) / 2);
    font-size: 24px;
    color: #FFF;
  }
}
