.banner {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: auto;

  img {
    object-fit: cover;
    width: 100%;
    max-height: 111px;
    border-radius: 15px;
    filter: brightness(70%);
  }

  p {
    position: absolute;
    top: 7px;
    left: 35px;
    font-size: 24px;
    width: 210px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: #FFF;
    line-height: 25px;
  }
}

@media (min-width: 768px) {
  .banner {
    max-width: 1200px;

    img {
      max-height: 180px;
    }

    p {
      font-size: 38px;
      top: 40px;
      left: calc(50% - 270px);
      width: 600px;
    }
  }
}


@media (min-width: 1200px) {
  .banner {
    img {
      max-height: 223px;
    }

    p {
      font-size: 48px;
      width: 800px;
      top: 50px;
      left: calc(50% - 345px);
    }
  }
}
